*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

.input {
  border: none;
  outline: none;
  background: white;
  color: black;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.bold {
  font-weight: 800;
}

.semibold {
  font-weight: 500;
}

.f65 {
  font-size: 65px;
}

.f30 {
  font-size: 30px;
}

.no-float {
  float: none;
}

.f14 {
  font-size: 14px;
}

.display-inline {
  display: inline-block;
}

.nopadding {
  padding: 0px;
}

.padding10 {
  padding: 10px;
}

.voffset4 {
  padding-top: 4vh;
}

.white-border {
  border: 1px solid white;
}

.ghost-button {
  background-color: transparent;
}

.white {
  color: white;
}
